import { Component, OnInit } from '@angular/core';
import {ServiceStatus} from '../services/status.service'
import {ServiceMyHttp} from '../services/my.http.service'
import {Router} from '@angular/router';
import { ToastrService } from 'ngx-toastr';

class UserLogin{
  public identificador: string;
  public password: string;
  constructor(){}
}


@Component({
  selector: 'app-home',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit { 

  // tslint:disable-next-line:whitespace
  constructor(private ServiceStatus:ServiceStatus,
    private ServiceMyHttp:ServiceMyHttp,private router: Router,
    private toastr: ToastrService) { }

  user = new  UserLogin();

  ngOnInit() {

  }

  myFunc(){
    this.ServiceStatus.TestConnect();
  }



  async onSubmit() {
    
    this.ServiceStatus.HiddenmsgErrorBool=true;
      
      const login = await this.ServiceMyHttp.login(this.user);

      if (login) {
        this.toastr.success('Correcto','Conectado correctamente',  {
          timeOut: 3000
        });
        this.router.navigateByUrl('/home', {skipLocationChange: true});
      } else {
        this.toastr.error('usuario / password incorrecto!', 'Error!', {
          timeOut: 0
        });
        
      }

      const a = '';

  }

  // TODO: Remove this when we're done
  get diagnostic() { return JSON.stringify(this.user); }

}
